<template>
  <div class="index-page">
    <div style="margin: 5px 0;display: flex;">
    <el-input
      placeholder="根据url筛选"
      v-model="filterUrl"
      clearable>
    </el-input>
    <el-button style="margin-left: 10px;" @click="clear()" type="danger">清空列表</el-button>
    </div>
    <el-table
      :data="getTableList"
      border
      @row-click="openDrawer"
      style="width: 100%">
      <el-table-column
        prop="url"
        label="地址">
      </el-table-column>
      <el-table-column
        prop="method"
        label="类型"
        width="100">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        width="100">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="200">
        <template slot-scope="scope">
          <el-button
            @click.stop="editMock(scope.row)"
            type="text"
            :disabled="scope.row.status === 'pending'"
            size="small">编辑mock
            </el-button>
          <el-button
            v-if="scope.row.isUseMock"
            @click.stop="cancelMock(scope.row)"
            type="text"
            size="small">取消mock
            </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-drawer
      :title="currentUrl"
      size="70%"
      :visible.sync="drawerIsShow"
      :before-close="handleClose">
      <el-input
        style="width: 90%;height: 90%;margin-left: 5%;"
        :disabled="disabled"
        type="textarea"
        placeholder="请输入返回数据"
        v-model="mockString"
        resize="none"
      >
      </el-input>
      <el-row style="margin-top: 10px;text-align: right;padding-right: 5%;">
        <el-button v-show="!disabled" type="primary" @click="saveMock">保存mock数据</el-button>
      </el-row>
    </el-drawer>
  </div>
</template>
<script>
/* eslint-disable no-return-assign */
export default {
  name: 'index',
  channel: void 0,
  data () {
    return {
      tableData: [],
      interceptMap: {},
      drawerIsShow: false,
      mockString: '',
      disabled: true,
      currentUrl: '',
      filterUrl: ''
    }
  },
  mounted () {
    this.setListener()
  },
  computed: {
    getTableList: function () {
      return this.tableData.filter(item => this.filterUrl ? ~item.url.indexOf(this.filterUrl) : true)
    }
  },
  methods: {
    setListener () {
      const self = this
      self.channel = chrome.runtime.connect({
        name: chrome.devtools.inspectedWindow.tabId.toString()
      })
      self.channel.onMessage.addListener(({type, data}) => {
        switch (type) {
          case 'clear':
            self.tableData = []
            break
          case 'push':
            const { url, method = 'GET', status = 'pending', requestId, isUseMock = false } = data
            const request = self.tableData.find(item => item.requestId === requestId)
            if (request) return
            self.tableData.push({url, method: method.toUpperCase(), status, requestId, isUseMock})
            break
          case 'update':
            const { response } = data
            const tableData = [...self.tableData]
            tableData.forEach(item => {
              if (item.requestId === data.requestId) {
                item.response = (typeof response === 'string' && data.status == 200)
                  ? JSON.parse(response) : response
                item.status = data.status
                item.isUseMock = data.isUseMock
              }
            })
            self.tableData = tableData
            break
          default :
            break
        }
      })
    },
    editMock (row) {
      this.mockString = JSON.stringify(row.response, null, 2)
      this.currentUrl = row.url
      this.drawerIsShow = true
      this.disabled = false
    },
    handleClose () {
      this.drawerIsShow = false
    },
    openDrawer (row) {
      this.disabled = true
      this.mockString = JSON.stringify(row.response, null, 2)
      this.drawerIsShow = true
    },
    saveMock () {
      const data = {
        url: this.currentUrl.split('?')[0],
        data: JSON.stringify(JSON.parse(this.mockString))
      }
      this.channel.postMessage({ type: 'saveMock', data })
      this.drawerIsShow = false
    },
    cancelMock (row) {
      this.channel.postMessage({ type: 'cancelMock', data: { url: row.url.split('?')[0] } })
      const tableData = this.tableData
      tableData.forEach(item => {
        if (item.requestId === row.requestId) {
          item.isUseMock = false
        }
      })
      this.tableData = tableData
      this.drawerIsShow = false
    },
    clear () {
      this.tableData = []
    }
  }
}
</script>
<style scoped>
  .index-page >>> .el-textarea__inner {
    width: 100%;
    height: 100%;
    outline: none;
  }
</style>



