import Vue from 'vue'
import App from './App'
import router from './router'
import {
  Button,
  Table,
  TableColumn,
  Drawer,
  Input,
  Row,
  Icon
} from 'element-ui'

Vue.use(Button)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Drawer)
Vue.use(Input)
Vue.use(Icon)
Vue.use(Row)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



